    white-space: pre-wrap !important;
    border: 1px solid #adb5bd;
    page-break-inside: avoid;
  }
  blockquote {
    border: 1px solid #adb5bd;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr,
  img {
    page-break-inside: avoid;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
  @page {
    size: a3;
  }

  body,
  .container {
    min-width: 992px !important;
  }
  .navbar {
    display: none;
  }
  .table {
    border-collapse: collapse !important;
    td,
    th {
      background-color: #fff !important;
    }
  }
  .table-bordered {
    th,
    td {
      border: 1px solid #dee2e6 !important;
    }
  }
  .table-dark {
    color: inherit;
    th,
    td,
    thead th,
    tbody + tbody {
      border-color: #dee2e6;
    }
  }
  .table .thead-dark th {
    color: inherit;
    border-color: #dee2e6;
  }
}

/*************************** Pillar Styles ****************************/

body {
  font-family: 'Roboto', sans-serif;
  background: #f7f8fa;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #434e5e;
}

.theme-bg-primary {
  background-color: #22a162;
}

.theme-bg-primary-darken {
  background-color: #104d2f;
}

a.theme-btn-on-bg,
.theme-btn-on-bg {
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
  border: none;
}

a.theme-btn-on-bg {
  &:hover,
  &:focus,
  &:active {
    background: rgba(0, 0, 0, 0.4);
    border-color: transparent;
    border: none;
  }
}

.theme-btn-on-bg {
  &:hover,
  &:focus,
  &:active {
    background: rgba(0, 0, 0, 0.4);
    border-color: transparent;
    border: none;
  }
}

.progress {
  border-radius: 2px;
}

.theme-progress-bar-dark {
  background-color: #22a162;
}

.resume-wrapper {
  padding-top: 30px;
  padding-bottom: 60px;
}

.resume-wrapper-inner {
  max-width: 1000px;
}

.resume-header {
  background: #22a162;
  color: rgba(255, 255, 255, 0.9);
  height: 220px;
  .picture {
    width: 220px;
  }
  .name {
    font-size: 2rem;
    letter-spacing: 0.175rem;
    font-weight: 900;
  }
  .title {
    font-size: 1.5rem;
  }
  a,
  .btn-anchor {
    color: rgba(255, 255, 255, 0.6) !important;
    &:hover {
      color: white !important;
    }
  }
}

.resume-social {
  font-size: 0.875rem;
  .fa-container {
    font-size: 1rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    border-radius: 0.125rem;
    background: white;
    color: #22a162;
  }
}

.resume-section-title {
  font-size: 1.25rem;
  position: relative;
  color: #22a162;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1.5px;
    background: #8d9aad;
  }
}

.resume-section-content {
  color: #58677c;
}

.resume-timeline {
  padding-left: 2rem;
  &:before {
    content: '';
    display: inline-block;
    width: 3px;
    height: 100%;
    background: #a3ecc8;
    left: 6px;
    top: 4px;
    position: absolute;
  }
}

.resume-timeline-item:before {
  content: '';
  display: inline-block;
  position: absolute;
  left: -32px;
  top: 3px;
  width: 15px;
  height: 15px;
  border: 4px solid #2bcb7c;
  background: white;
  border-radius: 50%;
}

.resume-progress {
  height: 0.6rem;
}

.resume-company-name {
  color: #58677c;
  font-size: 0.875rem;
  font-weight: 500;
}

.resume-company-logo {
  height: 45px;
  width: auto;
  visibility: hidden;
}

@media (min-width: 768px) {
  .resume-company-logo {
    visibility: visible;
  }
}

.resume-position-time {
  font-size: 0.875rem;
  color: #aab4c3;
}

.resume-position-title {
  font-size: 1.125rem;
  color: #434e5e;
}

.resume-timeline-item-desc-heading {
  font-size: 1rem;
}

.resume-skills-cat {
  font-size: 1rem;
  color: #434e5e;
}

.resume-skill-name {
  font-size: 0.8125rem;
  color: #434e5e;
}

.resume-degree {
  color: #434e5e;
}

.resume-degree-org {
  font-size: 0.875rem;
}

.resume-degree-time {
  color: #aab4c3;
  font-size: 0.875rem;
}

.resume-award-icon {
  left: 0;
  top: 4px;
  color: #aab4c3;
}

.resume-award-name {
  font-weight: bold;
  color: #434e5e;
}

.resume-award-desc {
  font-size: 0.875rem;
}

.resume-lang-name {
  color: #434e5e;
}

.footer .fa-heart {
  color: #fb866a;
}

@media (max-width: 991.98px) {
  .resume-wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }
  .resume-header {
    height: auto;
    .picture {
      width: 120px;
      border-radius: 4px;
    }
  }
}

.resume-timeline-item-header {
  position: relative;
  .top-right-icon {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.interests-section .resume-section-content {
  font-size: 0.9rem;
}

.btn-anchor {
  padding: 0;
  border: 0;
  cursor: pointer;
}

/*************************** Print Styles ****************************/

@media print {
  .pt-md-0 {
    padding-top: 0 !important;
  }
  .flex-md-row {
    flex-direction: row !important;
  }
  .ml-md-auto {
    margin-left: auto !important;
  }
  .col-lg-3 {
    flex: 0 0 25%;
    max-width: 25%;
  }
  .col-lg-9 {
    flex: 0 0 75%;
    max-width: 75%;
  }
  .resume-header {
    height: 220px;
    .picture {
      width: 220px;
      border-radius: 0;
    }
  }
  .resume-body {
    .list-inline-item {
      margin-bottom: 0.5rem;
    }
  }
}
